.tech-stack-box {
    background-image: linear-gradient(to bottom right, var(--tech-stack-box-first-color), var(--tech-stack-box-second-color))
}

.tech-stack-wrapper {
    justify-content: center;
    flex-wrap: wrap;
    gap: 50px;
    margin-top: 50px
}

.tech-stack-box,
.tooltip {
    align-items: center;
    display: flex
}

.tech-stack-box {
    width: 160px;
    height: 160px;
    justify-content: center;
    border-radius: 30px;
    border: 1px solid var(--tech-stack-box-border-color);
    position: relative
}

.tech-stack-box img {
    width: 80%
}

.tech-stack-box:hover .tooltip {
    opacity: 1;
    transition-duration: .3s
}

.tooltip {
    position: absolute;
    top: -20px;
    opacity: 0;
    background: linear-gradient(to bottom right, var(--tech-stack-box-first-color), var(--tech-stack-box-second-color));
    border: 1px solid var(--tech-stack-box-border-color);
    padding: 5px 10px;
    border-radius: 5px;
    justify-content: center;
    transition-duration: .2s;
    pointer-events: none;
    letter-spacing: .5px;
    font-size: 1.6rem;
    backdrop-filter: blur(5px);
    font-weight: 500
}

.cta,
.cta span,
.cta svg,
.project-box,
footer {
    position: relative
}

.projects-section-container {
    padding: 150px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%
}

.project-boxes-div {
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
    padding: 50px
}

.cta:hover:before,
.project-box-wrapper {
    width: 100%
}

.project-box {
    width: 100%;
    height: 400px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    transition-duration: .3s;
    border-radius: 30px;
    border: 1px solid var(--tech-stack-box-border-color)
}

.info-div {
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 20px;
    padding: 0 40px
}

.faviconforProject {
    width: 30px;
    border-radius: 50%
}

.faviconforProjectAquaregia {
    width: 20px
}

.image-div {
    width: 50%;
    height: 100%;
    padding-top: 70px;
    padding-left: 10px
}

.image-div img {
    height: 100%;
    border-top-left-radius: 30px
}

.ProjectHeading {
    color: var(--color-white);
    font-size: 3.4rem;
    font-weight: 700
}

.ProjectDescription {
    color: gray;
    font-size: 1.7rem;
    font-weight: 400
}

.project-buttons {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 20px
}

.github-redirect {
    width: 50px
}

.github-redirect img {
    width: 45px;
    height: auto;
    transition: .3s;
    filter: brightness(2)
}

.github-redirect:hover img {
    filter: brightness(4)
}

.cta {
    padding: 12px 18px;
    transition: .2s;
    border: none;
    background: 0 0;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: flex-start
}

.cta::before {
    content: "";
    position: absolute;
    left: 0;
    display: block;
    border-radius: 50px;
    background: #2b2f4e;
    width: 45px;
    height: 45px;
    transition: .3s
}

.cta span {
    font-size: 1.8rem;
    font-weight: 600;
    letter-spacing: .05em;
    color: var(--color-light-blue)
}

.cta svg {
    top: 0;
    margin-left: 10px;
    fill: none;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke: var(--color-light-blue);
    stroke-width: 2;
    transform: translateX(-5px);
    transition: .3s
}

.cta:hover svg {
    stroke: white;
    transform: translateX(0)
}

.cta:active {
    transform: scale(.95)
}

#backtotopbutton {
    position: fixed;
    right: 10px;
    bottom: 0;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: none;
    width: 3rem;
    height: 15rem;
    z-index: 101;
    text-align: center;
    transition: .4s;
    display: none
}

#backtotopbutton article {
    font-size: 1.5rem;
    color: var(--color-white);
    font-weight: 400;
    writing-mode: vertical-rl;
    mix-blend-mode: difference
}

#backtotopbutton:hover {
    transform: translate(0, -20px);
    text-decoration: line-through var(--color-white)
}

footer {
    width: 100%;
    height: fit-content;
    border-top: 1px solid var(--tech-stack-box-border-color)
}

.footer-background {
    width: 100%;
    height: 100vh;
    background-color: #0b0b0b;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    z-index: 1
}

.footer-blob {
    width: 100%;
    height: 200px;
    background-color: var(--color-light-purple);
    border-radius: 50%;
    filter: blur(100px);
    opacity: .7
}

.footer-foreground {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    backdrop-filter: blur(10px)
}

.footercontainer {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end
}

.two-words {
    color: #b3b3b3;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100px
}

.getintouch-heading,
.logos {
    height: 80px;
    width: 100%;
    display: flex
}

.two-words article {
    font-size: 3rem
}

.social-media-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%
}

.social-media-container a {
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    padding: 10px;
    border-radius: 50%;
    border: 2px solid #e2dede;
    position: relative;
    box-shadow: 0 0 0 #8000ff inset;
    transition-duration: .3s
}

.getintouch-heading {
    align-items: center;
    justify-content: center
}

.getintouch-heading article {
    font-size: 2rem;
    background: linear-gradient(-45deg, var(--color-light-blue), var(--color-light-purple), var(--color-light-blue), var(--color-light-purple));
    background-clip: text;
    -webkit-background-clip: text;
    font-weight: 700
}

.fa-copyright,
.footer-bottom article {
    font-size: 1.5rem;
    color: #b0b0b0
}

.logos {
    gap: 30px;
    justify-content: center;
    align-items: center
}

.SocialHandle {
    fill: white;
    width: 60%
}

#GmailLogo {
    height: 70%
}

.social-media-container a:hover {
    box-shadow: 0 0 50px 100px #8000ff inset;
    transition-duration: 1s;
    border: none
}

.social-media-container a::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    left: 0;
    border-radius: 50%;
    transform: scale(0);
    background-color: transparent;
    transition-duration: .3s;
    z-index: -1
}

.social-media-container a:hover::before {
    transform: scale(1);
    transition-duration: .3s
}

.footer-avatar-container {
    width: 220px;
    margin-top: 20px;
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: center
}

.footer-avatar-img {
    width: 100%;
    z-index: 2
}

.footer-avatar-face {
    position: absolute;
    width: 100%;
    height: 97%;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 18px;
    padding-left: 5px
}

.footer-avatar-eye {
    width: 50px;
    height: 50px;
    background-color: #dfd2d2;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center
}

.footer-pupil {
    width: 20px;
    height: 20px;
    background-color: #000;
    border-radius: 50%;
    border: 5px solid #413333;
    filter: blur(.6px)
}

.footer-right-eye {
    box-shadow: 15px 15px 5px rgba(0, 0, 0, .233) inset, 10px 10px 5px rgba(41, 33, 33, .644) inset;
    position: relative
}

.footer-left-eye {
    box-shadow: -10px -15px 5px rgba(0, 0, 0, .349) inset;
    position: relative
}

.footer-avatar-eye::before {
    position: absolute;
    content: "";
    width: 2px;
    height: 5px;
    border-radius: 50%;
    background-color: rgba(214, 214, 214, .726);
    z-index: 2;
    filter: blur(.2px);
    left: 15px;
    transform: rotate(45deg)
}

.footer-bottom {
    background-color: #0b0b0b;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 80px;
    z-index: 3
}

.tonechange {
    color: #000
}

@media screen and (max-width:1920px) {
    html {
        font-size: 60%
    }
}

@media screen and (max-width:1400px) {
    html {
        font-size: 60%
    }
}

@media screen and (max-width:1200px) {
    html {
        font-size: 60%
    }

    .projects-section-div,
    .skills-section,
    .text-content {
        width: 80%
    }

    .about-section {
        width: 80%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center
    }

    .logo:hover {
        transform: translateY(200px);
        transition-duration: 1s
    }
}

@media screen and (max-width:1150px) {
    html {
        font-size: 60%
    }
}

@media screen and (max-width:998px) {

    #hamburger-button,
    .hamburger {
        background-color: transparent
    }

    .mobile-navbar-tabs-ul a,
    .mobiletogglemenu {
        text-decoration: none
    }

    .activeThismobiletab,
    .mobile-navbar-tabs-ul a {
        color: var(--color-white)
    }

    html {
        font-size: 55%
    }

    .blob,
    .cursor-inner,
    .cursor-outer,
    .navbar-tabs-ul {
        display: none
    }

    .stopscrolling {
        overflow: hidden
    }

    .hamburger {
        position: fixed;
        top: 0;
        width: 100vw;
        height: 115px;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        z-index: 9998
    }

    .activeThismobiletab:before,
    .burger-bar,
    .hamburger-animation1,
    .hamburger-animation2,
    .hamburger-animation3 {
        background-color: var(--color-white)
    }

    .hamburgerbase {
        width: 80%;
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        padding: 20px
    }

    #hamburger-button {
        font-size: 3rem;
        font-weight: 800;
        width: 4.5rem;
        height: 3.5rem;
        border: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between
    }

    #burger-bar1,
    #burger-bar2,
    #burger-bar3 {
        height: 3px;
        border-radius: 0;
        transition-duration: .3s
    }

    #burger-bar1,
    #burger-bar3 {
        width: 60%
    }

    #burger-bar2 {
        width: 100%
    }

    .hamburger-animation1 {
        transform: rotate(45deg) scaleX(1.7);
        transition-duration: .3s;
        transform-origin: left
    }

    .hamburger-animation2 {
        transform: scaleX(0)
    }

    .hamburger-animation3 {
        transform: rotateZ(-45deg) scaleX(1.7);
        transform-origin: left;
        transition-duration: .3s
    }

    .mobiletogglemenu {
        background-color: transparent;
        z-index: 9990;
        height: 100%;
        width: 100%;
        position: fixed;
        top: 0;
        right: 0;
        list-style: none;
        font-size: 2rem;
        cursor: pointer;
        display: flex;
        transform: translate(1000px);
        transition-duration: 1s
    }

    .show-toggle-menu {
        transform: translate(0);
        transition-duration: .6s
    }

    .mobile-navbar-tabs-ul {
        background-color: var(--color-dark-mode);
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 70px;
        box-shadow: 0 0 30px rgba(0, 0, 0, .176)
    }

    .mobile-navbar-tabs-li {
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.8rem;
        font-weight: 600;
        position: relative;
        cursor: pointer;
        font-family: fira code;
        height: 30px;
        width: fit-content
    }

    .about-info,
    .dp,
    .info-dp-section {
        width: 100%;
        display: flex
    }

    .info-dp-section {
        flex-direction: column;
        margin-top: 30px;
        gap: 30px
    }

    .about-info {
        flex-direction: column
    }

    .dp {
        align-items: center;
        justify-content: center
    }

    .activeThismobiletab:before {
        content: "";
        position: absolute;
        height: 10px;
        width: 10px;
        left: -20px;
        border-radius: 50%
    }

    .projects-section-div {
        width: 80%
    }

    .project-box {
        flex-direction: column;
        height: 800px
    }

    .image-div,
    .info-div {
        width: 100%;
        height: 50%
    }

    .image-div {
        padding: 0 0 0 80px
    }
}

@media screen and (max-width:768px) {
    html {
        font-size: 50%
    }

    .text-content {
        padding-left: 0;
        width: 80%
    }

    #hello-friend {
